"use client";
// import { div } from "@/components/ui/div"
import { ArrowUpRight } from "lucide-react";
import { Fragment, useState } from "react";

// solution > technology----------------------------
const technologyContent = [
  {
    cms: {
      title: "CMS",
      items: [
        {
          icon: "",
          name: "Contentful",
        },
        {
          icon: "",
          name: "Sanity",
        },
        {
          icon: "",
          name: "Builder",
        },
        {
          icon: "",
          name: "DatoCMS",
        },
        {
          icon: "",
          name: "Storyblok",
        },
        {
          icon: "",
          name: "HubSpot CMS",
        },
        {
          icon: "",
          name: "Webflow",
        },
        {
          icon: "",
          name: "Wordpress",
        },
      ],
    },
  },
  {
    framework: {
      title: "FRAMEWORK",

      items: [
        {
          icon: "",
          name: "NextJS",
        },
        {
          icon: "",
          name: "Gatsby",
        },
        {
          icon: "",
          name: "Deployment",
        },
        {
          icon: "",
          name: "Vercel",
        },
        {
          icon: "",
          name: "Netlify",
        },
      ],
    },
  },
];

// solutions > use case-----------------------------

const solutionsUseCase = [
  {
    icon: "icon",
    heading: "Support In-House Engineers",
    text: "Scale your dev team.",
  },
  {
    icon: "icon",
    heading: "Increase Website Conversions",
    text: "Convert more visitors.",
  },
  {
    icon: "icon",
    heading: "Improve Brand Consistency",
    text: "Strengthen your brand.",
  },
  {
    icon: "icon",
    heading: "Boost Website Performance",
    text: "Speed up your site.",
  },
  {
    icon: "icon",
    heading: "Increase Website Traffic",
    text: "Get more visitors.",
  },
];

// Drop Down solution menu
const solutionDropdown = [


  // scope tab submenu-------------------------------------------------
  {
    icon: "",
    title: "Scope",
    content: (
      <div className={"w-full"}>
        <div className={"flex flex-row"}>
          <div>
            <p>Website Redesigns</p>
            <ArrowUpRight />
          </div>
          <div>
            <p>Website Migrations</p>
            <ArrowUpRight />
          </div>
        </div>
        <div>
          <p>Ongoing WebsiteServices</p>
          <ArrowUpRight />
        </div>
      </div>
    ),
  },



  // Technology tab submenu-------------------------------------------------

  {
    icon: "",
    title: "Technology",
    content: (
      <div className="flex flex-row">
        <div>
          <h3>{technologyContent[0].cms.title}</h3>
          {technologyContent[0].cms.items.map((item, index) => {
            return <p key={index}>{item.name}</p>;
          })}
        </div>
        <div>
          <h3>{technologyContent[1].framework.title}</h3>
          {technologyContent[1].framework.items.map((item, index) => {
            return <p key={index}>{item.name}</p>;
          })}
        </div>
      </div>
    ),
  },



  // Industry tab submenu-------------------------------------------------

  {
    icon: "",
    title: "Industry",
    content: (
      <div className="w-full flex flex-row">
        <div className="w-1/2">
          <p>SaaS</p>
          <p>FinTech</p>
          <p>AI/ML</p>
        </div>
        <div className="w-1/2">
          <p>Developer Tools</p>
          <p>Web3</p>
          <p>Enterprise Software</p>
        </div>
      </div>
    ),
  },


  // Use Case tab submenu-------------------------------------------------

  {
    icon: "",
    title: "Use Case",
    content: (
      <div className="w-full flex flex-row flex-wrap">
        {solutionsUseCase.map((item, index) => {
          return (
            <div className=" border border-blue-500 w-1/2">
              <div>
                <p>{item.icon}</p>
                <h4>{item.heading}</h4>
              </div>
              <div className="w-1/2">{item.text}</div>
            </div>
          );
        })}
      </div>
    ),
  },



    // Stage tab submenu-------------------------------------------------

  {
    icon: "",
    title: "Stage",
    content: (
      <div className="flex flex-row">
        <div>
          <h4>Enterprize</h4>
          <ArrowUpRight />
        </div>
        <div>
          <h4>Start-ups</h4>
          <ArrowUpRight />
        </div>
      </div>
    ),
  },
];



// Function ---------------------------------------------------
export default function Solutions() {
  const [activeIndex, setActiveIndex] = useState(0);

  return (
    <Fragment>
      <div className={"flex flex-row w-1/3"}>
        <div className={"w-full"}>
          {solutionDropdown.map((item, index) => {
            return (
              <h4
                className="cursor-pointer"
                key={index}
                onClick={() => setActiveIndex(index)}
              >
                {item.title}
              </h4>
            );
          })}
        </div>
      </div>
      <div className={"w-2/3"}>{solutionDropdown[activeIndex].content}</div>
    </Fragment>
  );
}
